body{
    margin: 0px 0px 0px 0px;
}

.main-container{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    background: #D4E6DD;
}

/* PART I */

.left-container{
    width: 25%;
    height: 90%;
    background: white;
    border-radius: 30px;
}

/* SUBPART I.1 */

.top-sub-container{
    width: 100%;
    height: 30%;
}

.name-logo{
    display: flex;
    position: relative;
    left: 10%;
    top: 20%;
}

.name-logo span {
    font-family: 'Spartan';
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 26px;
    letter-spacing: 0.03em;
    color: #F9BD25;
}

.name-logo img{
    position: relative;
    top: 7px;
}

/* END OF SUBPART I.1 */

.bot-sub-container{
    width: 100%;
    height: 70%;
    background-color: black;
}

/* END OF PART I */

.mid-container{
    width: 32%;
    height: 90%;
    
}

.right-container{
    width: 32%;
    height: 90%;
    background: white;
    border-radius: 40px;
}
